define('fusor-ember-cli/controllers/openstack/overcloud', ['exports', 'ember', 'fusor-ember-cli/mixins/deployment-controller-mixin', 'fusor-ember-cli/mixins/needs-deployment-mixin'], function (exports, Ember, DeploymentControllerMixin, NeedsDeploymentMixin) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend(DeploymentControllerMixin['default'], NeedsDeploymentMixin['default'], {

    isCloudForms: Ember['default'].computed.alias("deploymentController.isCloudForms"),

    nextStepRouteNameOvercloud: Ember['default'].computed('isCloudForms', function () {
      if (this.get('isCloudForms')) {
        return 'cloudforms';
      } else {
        return 'subscriptions';
      }
    }),

    isValidPrivateNetworkRange: Ember['default'].computed('model.openstack_overcloud_private_net', function () {
      // TODO
      return true;
    }),

    isValidPrivateFloatRange: Ember['default'].computed('model.openstack_overcloud_float_net', function () {
      // TODO
      return true;
    }),

    validOvercloudNetworks: Ember['default'].computed('model.openstack_overcloud_interface', 'model.openstack_overcloud_private_net', 'model.openstack_overcloud_float_net', 'isValidPrivateNetworkRange', 'isValidPrivateFloatRange', function () {
      return Ember['default'].isPresent(this.get('model.openstack_overcloud_interface')) && Ember['default'].isPresent(this.get('model.openstack_overcloud_private_net')) && Ember['default'].isPresent(this.get('model.openstack_overcloud_float_net')) && this.get('isValidPrivateNetworkRange') && this.get('isValidPrivateFloatRange');
    }),

    disableNextOvercloud: Ember['default'].computed.not('validOvercloudNetworks')

  });

});