    deleted: DS['default'].attr('boolean'),
    deleted_at: DS['default'].attr('date'),
    disk_format: DS['default'].attr('string'),
    is_public: DS['default'].attr('boolean'),
    min_disk: DS['default'].attr('number'),
    min_ram: DS['default'].attr('number'),
