define('fusor-ember-cli/routes/openstack/overcloud', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({

    model: function model() {
      return this.modelFor('deployment');
    },

    setupController: function setupController(controller, model) {
      controller.set('model', model);
      if (Ember['default'].isBlank(controller.get('model.openstack_overcloud_interface'))) {
        controller.set('model.openstack_overcloud_interface', 'eth1');
      }
    }

  });

});